// frontend/src/app/questionarios/page.tsx (VERSÃO UNIFICADA)
'use client';

import { useEffect, useState } from "react";
import { useRouter } from "next/navigation";
import AdminAuthGuard from '../../components/auth/AdminAuthGuard'; // Ajuste o caminho se necessário
import api from "@/services/api";
import Link from "next/link";
import "../globals.css";
import "../questionario.css"; // Seu CSS específico para questionários
import { MoreVertical, Trash2, Edit3, Info, PlusIcon as LucidePlusIcon, ListChecks } from 'lucide-react';

// --- INTERFACES (Mantidas da sua versão funcional, mas ajustadas para o que o backend realmente envia) ---
interface OpcaoInterface {
  id: number;
  texto: string;
}

interface PerguntaDetalhadaInterface {
  id: number;
  enunciado: string;
  tipos: 'TEXTO' | 'MULTIPLA_ESCOLHA' | string; // Permitir string para flexibilidade
  opcoes?: OpcaoInterface[]; // Opcoes pode não vir sempre, especialmente para perguntas TEXTO
}

interface QuePergItemInterface {
  // id?: number; // ID da relação QuePerg, se o backend enviar
  pergunta: PerguntaDetalhadaInterface;
  // Adicione outros campos da relação QuePerg se enviados e necessários
}

interface QuestionarioInterface {
  id: number;
  titulo: string;
  created_at: string;
  updated_at: string;
  perguntas: QuePergItemInterface[]; // Backend DEVE incluir esta relação via QuePerg
  criador?: { nome: string; email: string };
  _count?: { avaliacoes: number };
}
// --- FIM DAS INTERFACES ---

// Componente que contém a lógica e o JSX da listagem
function ListQuestionariosContent() {
  const router = useRouter();
  const [questionarios, setQuestionarios] = useState<QuestionarioInterface[]>([]);
  const [menuAberto, setMenuAberto] = useState<number | null>(null);
  const [detalhesVisiveis, setDetalhesVisiveis] = useState<number | null>(null);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    setIsLoading(true);
    setError(null);

    // Esta chamada DEVE retornar os questionários com suas 'perguntas' (QuePerg) aninhadas,
    // e cada 'pergunta' dentro de QuePerg deve ter suas 'opcoes' se for MULTIPLA_ESCOLHA.
    // Isso é configurado no 'include' do Prisma no seu GetAllQuestionariosController.
    api.get("/questionarios")
      .then(response => {
        // Sanitizar os dados para garantir que q.perguntas e q.pergunta.opcoes sejam arrays
        const sanitizedData = response.data.map((q: QuestionarioInterface) => ({
          ...q,
          perguntas: (q.perguntas || []).map(qp => ({
            ...qp,
            pergunta: {
              ...qp.pergunta,
              opcoes: qp.pergunta.opcoes || []
            }
          }))
        }));
        setQuestionarios(sanitizedData);
      })
      .catch(err => {
        console.error("Erro ao buscar questionários:", err);
        if (err.response && (err.response.status === 401 || err.response.status === 403)) {
          setError("Acesso não autorizado. Faça login como administrador.");
        } else {
          setError("Erro ao buscar questionários. Verifique a conexão ou tente mais tarde.");
        }
      })
      .finally(() => {
        setIsLoading(false);
      });
  }, []);

  const formatDate = (isoDate: string) => {
    if (!isoDate) return "N/A";
    return new Date(isoDate).toLocaleString("pt-BR", {
      day: "2-digit", month: "2-digit", year: "numeric",
      hour: "2-digit", minute: "2-digit",
    });
  };

  const handleDeleteQuestionario = async (id: number) => {
    if (!window.confirm("Deseja realmente excluir este questionário? Suas perguntas e avaliações associadas também podem ser afetadas.")) return;
    try {
      await api.delete('/questionarios', { data: { id } });
      setQuestionarios(prev => prev.filter(q => q.id !== id));
      setMenuAberto(null); 
      alert("Questionário excluído com sucesso!");
    } catch (err) {
      console.error(err);
      alert("Erro ao excluir o questionário!");
    }
  };

  const handleEditQuestionario = (id: number) => {
    router.push(`/questionarios/${id}`); // Navega para a página de edição
  };

  const toggleMenu = (id: number, event?: React.MouseEvent) => {
    event?.stopPropagation(); 
    setMenuAberto(prevMenuAberto => (prevMenuAberto === id ? null : id));
    setDetalhesVisiveis(null); 
  };

  const toggleDetalhes = (id: number, event?: React.MouseEvent) => {
    event?.stopPropagation();
    setDetalhesVisiveis(prevDetalhesVisiveis => (prevDetalhesVisiveis === id ? null : id));
    setMenuAberto(null); 
  };

  if (isLoading) {
    return <div className="page-container center-content"><p className="text-text-muted">Carregando questionários...</p></div>;
  }

  if (error) {
    return <div className="page-container center-content"><p style={{ color: 'red' }}>{error}</p></div>;
  }

  return (
    <div className="page-container">
      <div className="table-header-actions"> {/* Do layout dela */}
        <h3 className="text-xl sm:text-2xl font-semibold text-foreground">
          Meus Questionários
        </h3>
        <div className="button-group"> {/* Mantendo sua classe para consistência, ou use o da dela se preferir */}
          <Link href="/questionarios/create" className="btn btn-primary inline-flex items-center">
            <LucidePlusIcon size={18} className="mr-2" />
            Novo Questionário
          </Link>
          {/* <Link href="/" className="btn btn-secondary">Voltar</Link>  // Botão voltar pode não ser necessário aqui */}
        </div>
      </div>

      {questionarios.length === 0 ? (
        <div className="text-center py-10 px-4 bg-card-background dark:bg-gray-800 rounded-lg shadow-md border border-border mt-6">
          <ListChecks className="mx-auto h-12 w-12 text-text-muted" strokeWidth={1.5} />
          <h3 className="mt-4 text-md font-medium text-foreground">Nenhum questionário cadastrado</h3>
          <p className="mt-1 text-sm text-text-muted">Crie um novo questionário para começar.</p>
          <div className="mt-6">
            <Link href="/questionarios/create" className="btn btn-primary inline-flex items-center">
              <LucidePlusIcon size={18} className="mr-2" />
              Criar Questionário
            </Link>
          </div>
        </div>
      ) : (
        <div className="questionarios-grid">
          {questionarios.map((q) => (
            <div
              key={q.id}
              className="questionario-card"
              role="button" 
              tabIndex={0} 
              onClick={() => handleEditQuestionario(q.id)}
              onKeyDown={(e) => { if (e.key === 'Enter' || e.key === ' ') { handleEditQuestionario(q.id); }}}
            >
              <div className="card-header">
                <h4>{q.titulo}</h4>
                <button
                  className="botao icon-button" 
                  title="Opções"
                  onClick={(e) => toggleMenu(q.id, e)}
                  aria-label="Opções do questionário"
                >
                  <MoreVertical size={20} />
                </button>
              </div>

              <div className="perguntas-list">
                {q.perguntas && q.perguntas.length > 0 ? (
                  q.perguntas.slice(0, 3).map((quePergItem) => (
                    <p
                      key={quePergItem.pergunta.id} 
                      className="enunciado-pergunta truncate" 
                      title={quePergItem.pergunta.enunciado}
                    >
                      {quePergItem.pergunta.enunciado}
                    </p>
                  ))
                ) : (
                  <p className="no-perguntas">Nenhuma pergunta adicionada.</p>
                )}
                {q.perguntas && q.perguntas.length > 3 && (
                  <p className="text-xs text-text-muted mt-1">
                    ...e mais {q.perguntas.length - 3}.
                  </p>
                )}
              </div>

              {menuAberto === q.id && (
                <div
                  className="menu-dropdown"
                  onClick={(e) => e.stopPropagation()}
                >
                  <button
                    className="botao edit-action w-full flex items-center"
                    onClick={(e) => { e.stopPropagation(); handleEditQuestionario(q.id); }}
                  >
                    <Edit3 size={16} className="mr-2" /> Editar
                  </button>
                  <button
                    className="botao details-action w-full flex items-center"
                    onClick={(e) => toggleDetalhes(q.id, e)}
                  >
                    <Info size={16} className="mr-2" /> Detalhes
                  </button>
                  <button
                    className="botao delete-action w-full flex items-center"
                    onClick={(e) => { e.stopPropagation(); handleDeleteQuestionario(q.id);}}
                  >
                    <Trash2 size={16} className="mr-2" /> Excluir
                  </button>
                </div>
              )}

              {detalhesVisiveis === q.id && (
                <div
                  className="detalhes"
                  onClick={(e) => e.stopPropagation()}
                >
                  <p><strong>ID:</strong> {q.id}</p>
                  {q.criador && <p><strong>Criador:</strong> {q.criador.nome} ({q.criador.email})</p>}
                  <p><strong>Criado em:</strong> {formatDate(q.created_at)}</p>
                  <p><strong>Última modificação:</strong> {formatDate(q.updated_at)}</p>
                  {q._count && <p><strong>Nº de Avaliações Associadas:</strong> {q._count.avaliacoes}</p>}
                  <button 
                    onClick={(e) => { e.stopPropagation(); setDetalhesVisiveis(null); }}
                    className="btn btn-sm btn-outline mt-2 w-full" // Usando classes de botão mais genéricas
                  >
                    Fechar Detalhes
                  </button>
                </div>
              )}
            </div>
          ))}
        </div>
      )}
    </div>
  );
}

// Componente de página que exportamos como padrão, que usa o AdminAuthGuard
export default function ProtectedListQuestionariosPage() {
  return (
    <AdminAuthGuard>
      <ListQuestionariosContent />
    </AdminAuthGuard>
  );
}

